@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;600;700;900&display=swap);
body {
  margin: 0px;
  min-height: 100%;
  width: 100%;
  font-family: 'Montserrat', sans-serif;
  background: white;
}

.App {
  text-align: center;
}

h1 {
  font-size: 3em;
  font-weight: 400;
  color: #404040;
}

form {
  margin-bottom: 2em;
}

input {
  width: 30em;
  height: 2em;
  font-family: 'Montserrat', sans-serif;
  border-radius: 2em;
  border-style: none;
  padding-left: 1em;
  border: solid 0.1em #404040;
  -webkit-appearance: none;
  box-shadow: none
}

input:focus {
  outline: none;
}

button:focus {
  outline: none;
}

.signinForm, .signupForm {
  display: inline-grid;
  margin-top: 6em;
}

.sign {
  margin: 1em;
  width: 20em;
}

.continue {
  height: 2.5em;
  width: 8em;
  text-transform: lowercase;
  margin-top: 1em;
  font-family: 'Montserrat', sans-serif;
  font-size: 0.9em;
  background: #a3b2a5;
  color: white;
  border-radius: 5em;
  cursor: pointer;
  box-shadow: none;
  border: solid 0.2em #a3b2a5;
}


@media screen and (max-width: 500px) { 

    h1 {
      font-size: 2em;
      margin-top: 1em;
      margin-bottom: -1em;
    }

}
.inputContainer {
  display: flex;
  margin-left: auto;
  margin-right: auto;
  justify-content: space-around;
  width: 8em;
}

.postButton {
  height: 2.5em;
  width: 6em;
  text-transform: lowercase;
  font-family: 'Montserrat', sans-serif;
  font-size: 0.8em;
  font-weight: 300;
  background: #a3b2a5;
  color: white;
  border-radius: 2em;
  cursor: pointer;
  box-shadow: none;
  border: solid 0.2em #a3b2a5;
}

.marginFix {
  margin-top: -1em;
  margin-bottom: 3em;
}


#showMe {
    -webkit-animation: cssAnimation 0s 5s forwards;
            animation: cssAnimation 0s 5s forwards;
    visibility: hidden;
  }
  
@-webkit-keyframes cssAnimation {
  to   { visibility: visible; }
}
  
@keyframes cssAnimation {
  to   { visibility: visible; }
}

@media screen and (max-width: 500px) { 

  .inputContainer {
    width: 8em;
    margin-top: 3em;
  }

  input {
    height: 2em;
    width: 22em;
    font-size: 0.9em;
    padding: 0.7em;
  }

  .marginFix {
    margin-top: -1em;
    margin-bottom: 2em;
  }

}
  
  
.board {
  text-align: center;
  display: flex;
  flex-flow: row wrap;
  margin: 0 7.5em 0 7.5em;
}

@media screen and (max-width: 500px) { 
  
  .board {
    margin: auto;
  }

}



  

.post {
    flex: 1 1 auto;
    text-align: center;
    min-width: 15em;
    max-width: 20em;
    min-height: 8em;
    height: -webkit-min-content;
    height: -moz-min-content;
    height: min-content;
    margin: 0.5em;
    background-color: #a3b2a5;
    border-radius: 0.5em;
    color: white;
    position: relative;
}

.message {
	color: #404040;
	text-align: left;
	margin: 2em 1em 4em 1em;
	line-height: 1.5em;
	font-size: 0.95em;
}

.delete {
	position: absolute;
    top: 0;
    border: none;
    font-family: 'Montserrat', sans-serif;
    font-weight: 400;
    cursor: pointer;
    margin: 0 0 0 0;
    border-radius: 5em;
    right: 0;
    margin-right: 0.3em;
	margin-top: 0.3em;
	color:  #404040;
}

.postInfo {
	display: flex;
    justify-content: space-between;
    margin-top: 6em;
    position: absolute;
    bottom: 0;
    width: 100%;
}

.date {
	font-size: 0.6em;
    line-height: 2.5em;
    margin-right: 1.5em;
}

.username {
	margin-left: 1em;
    font-size: 0.8em;
    font-style: italic;
    font-weight: 300;
	text-transform: capitalize;
}

@media screen and (max-width: 500px) { 
    
    .post {
        max-width: 21em;
        margin-left: 1.2em;
    }
}
.header {
  display: inline-flex;
  justify-content: flex-end;
  height: 3em;
  width: 100%;
  background-color: #404040;
}

.user {
  height: 3em;
  line-height: 2.5em;
  margin-left: 1em;
  color: #a3b2a5;
  font-size: 1.2em;
  font-weight: 400;
  text-transform: capitalize;
  
}

.user:hover {
  text-decoration: underline;
  cursor: pointer;
}

.signoutContainer{
  display: flex;
  height: 2em;
  flex-direction: row-reverse;
}
  
.signout {
  font-size: 2em;
  left: 88em;
  width: 8em;
  height: 6em;
  cursor: pointer;
  border: none;
  margin-right: 1em;
  border-radius: 0 0 2em 2em;
  font-family: 'Montserrat', sans-serif;
  font-size: 1em;
  background: #a3b2a5;
  color: white;
  -webkit-animation-name: grow;
          animation-name: grow;
  -webkit-animation-duration: 0.1s;
          animation-duration: 0.1s;

}

@-webkit-keyframes grow {
  0%   { height: 1em;}
  25%  { height: 2em;}
  50%  { height: 4em;}
  75%  { height: 6em;}
  100% { height: 6em;}
}

@keyframes grow {
  0%   { height: 1em;}
  25%  { height: 2em;}
  50%  { height: 4em;}
  75%  { height: 6em;}
  100% { height: 6em;}
}

.usericon {
  width: 1.5em;
  margin: 0 1em 0 0.5em;
}

@media screen and (max-width: 500px) { 

  .signout {
    height: 4em;
  }
  
  @-webkit-keyframes grow {
    0%   { height: 1em;}
    25%  { height: 1em;}
    50%  { height: 2em;}
    75%  { height: 3em;}
    100% { height: 4em;}
  }
  
  @keyframes grow {
    0%   { height: 1em;}
    25%  { height: 1em;}
    50%  { height: 2em;}
    75%  { height: 3em;}
    100% { height: 4em;}
  }

  .name {
    display: none;
  }

}

  

  

.dropdown {
    position: relative;
    display: inline-block;
}

.dropbtn {
    border-radius: 1em;
    color: white;
    font-size: 1em;
    width: 2em;
    height: 2em;
    border: none;
    cursor: pointer;
}

.settingsIcon {
    width: 2em;
    margin-left: -0.40em;
}

.dropdown-content {
    display: none;
    position: absolute;
    width: 6em;
    margin-left: 0.5em;
}

.dropdown-content div {
    width: 1em;
    float: left;
    padding: 0.4em;
    height: 1em;
    border-radius: 1em;
    margin: 0.1em
}

.dropdown:hover .dropdown-content {
    display: block;
    cursor: pointer;
}

 .dropdown-content div:hover {
    box-shadow:inset 0em 0em 0em 0.05em #606060;

}

.dropdown-content div:nth-child(1) { background: #c3ccd6; }
.dropdown-content div:nth-child(2) { background: #a3b2a5; }
.dropdown-content div:nth-child(3) { background: #d9c8d5; }

@media screen and (max-width: 500px) {  
    
    .settingsIcon {
        margin-left: -1em;
    }

}
.typewriter {
    margin-top: 8em;
}
    
.cursor {
    -webkit-animation:1s blink step-end infinite;
            animation:1s blink step-end infinite
}
    
  @keyframes blink {
      from,
      to {
          color: transparent
      }
      50% {
          color: #000
      }
  }
  
  @-webkit-keyframes blink {
      from,
      to {
          color: transparent
      }
      50% {
          color: #000
      }
  } 

  @media screen and (max-width: 500px) {
	.homeOptions {
		width: 90%;
		height: 20%;
	}

	.title {
		font-size: 1.5em;
		margin-bottom: 2em;
	}

	.signButton {
		height: 2em;
		width: 8em;
		margin: 0.5em auto 0.5em auto;
	}

	.typewriter {
		margin-top: 4em;
		font-size: 0.70em;
		padding:  1em 0.3em 3em 0.3em;
	  }
	
  }
