.homeOptions {
  display: flex;
  flex-direction: column;
  width: 50em;
  height: 30em;
  margin: 5em auto 0 auto;
  border: solid 0.1em #272727;
  border-radius: 2em;
}
    
.title {
  margin: 1.5em 0 1em 0;
}

.signButton {
  height: 3em;
  margin: 1em 16em 0 16em;
  font-family: 'Montserrat', sans-serif;
  font-size: 1em;
  background: #a3b2a5;
  color: white;
  border-radius: 5em;
  cursor: pointer;
  box-shadow: none;
  border: solid 0.2em #a3b2a5;
}
  


@media screen and (max-width: 500px) {
	.homeOptions {
		width: 90%;
		height: 20%;
	}

	.title {
		font-size: 1.5em;
		margin-bottom: 2em;
	}

	.signButton {
		height: 2em;
		width: 8em;
		margin: 0.5em auto 0.5em auto;
	}
	
  }